body{
    padding: 0;
    margin: 0;
}
.navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid rgb(73, 244, 34);
    height: 40px;
}
.navbar h1{
    margin: 0;
    margin-left:30px;
    color: rgb(73, 244, 34);
}
.links{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    padding: 5px;
    margin-right: 40%;
}

.navbar a{
    color: black;
    text-decoration: none;
    /* margin: 5px; */
    padding: 2px 8px 2px 8px;
    font-size: 20px;
}
